  // ì†Œì„¤ ì˜ì—­ (Store Content Style)
  .novel-scroll-area {
    max-width: 900px;
    margin: 0 auto;
    padding: 40px;
    min-height: calc(100vh - 62px);
    padding-bottom: 100px;
    
    // ëª¨ë°”ì¼ ìµœì í™”
    @media (max-width: 768px) {
      padding: 24px 16px;
      padding-bottom: 80px;
    }
    
    @media (max-width: 480px) {
      padding: 20px 12px;
      padding-bottom: 60px;
    }
    
    .novel-header {
      text-align: center;
      padding: 48px 0 48px;
      margin-bottom: 48px;
      position: relative;
      overflow: hidden;
      
      // ë°°ê²½ ê·¸ë¼ë°ì´ì…˜ ë ˆì´ì–´
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 120%;
        height: 100%;
        background: linear-gradient(135deg, 
          rgba(102, 126, 234, 0.08) 0%, 
