        z-index: 0;
      }
      
      // í•˜ë‹¨ ì¥ì‹ ë¼ì¸
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        height: 3px;
        background: linear-gradient(90deg, 
          transparent 0%, 
          #667eea 20%, 
          #764ba2 50%, 
          #667eea 80%, 
          transparent 100%);
        border-radius: 3px;
