    display: flex;
    flex-direction: column;
    
    .panel-header {
      padding: 20px 48px; // íŒ¨ë”© ê°ì†Œ (32px -> 20px)
      background: var(--bg-secondary);
      color: var(--text-primary);
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 2px solid var(--border-color);
      flex-shrink: 0;
      
      h3 {
        margin: 0;
        font-size: 1.8rem; // í°íŠ¸ í¬ê¸° ê°ì†Œ (2rem -> 1.8rem)
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.5px;
      }
      
      .close-btn {
        background: transparent;
        border: 1px solid var(--border-color);
        color: var(--text-primary);
        font-size: 1.8rem; // í°íŠ¸ í¬ê¸° ê°ì†Œ (2rem -> 1.8rem)
        cursor: pointer;
        width: 42px; // í¬ê¸° ê°ì†Œ (48px -> 42px)
        height: 42px; // í¬ê¸° ê°ì†Œ (48px -> 42px)
        border-radius: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        flex-shrink: 0;
        
        &:hover {
          background: var(--accent-primary);
          color: white;
          border-color: var(--accent-primary);
          transform: rotate(90deg);
        }
      }
    }
    
    .panel-content {
      padding: 40px 60px; // íŒ¨ë”© ì¦ê°€
      flex: 1;
      overflow-y: auto;
      
      &::-webkit-scrollbar {
        width: 6px;
      }
      
      &::-webkit-scrollbar-track {
        background: var(--bg-secondary);
      }
      
      &::-webkit-scrollbar-thumb {
        background: var(--border-color);
        border-radius: 3px;
        
        &:hover {
          background: var(--text-secondary);
        }
      }
      
      > div {
        padding: 16px 20px;
        margin: 12px 0;
        background: var(--bg-secondary);
        border-radius: 8px;
        border-left: 3px solid var(--accent-primary);
        color: var(--text-primary);
        font-size: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: all 0.3s ease;
        
        &:hover {
          background: var(--bg-primary);
          border-left-width: 4px;
          transform: translateX(4px);
          box-shadow: 0 2px 8px var(--shadow);
        }
        
        button {
          padding: 8px 16px;
          background: var(--accent-primary);
          border: none;
          border-radius: 6px;
          color: white;
          font-weight: 600;
          cursor: pointer;
          transition: all 0.3s ease;
          margin-left: 10px;
          
          &:hover {
            background: var(--accent-secondary);
            transform: translateY(-2px);
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
          }
          
          &:disabled {
            opacity: 0.4;
            cursor: not-allowed;
            transform: none;
          }
        }
      }
    }
  }
}

