.battle-section {
  width: 100%;
  padding: 32px 24px;
  margin: 24px 0;
  background: var(--bg-secondary);
  border-radius: 12px;
  border: 1px solid var(--border-color);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  
  .battle-header {
    text-align: center;
    margin-bottom: 24px;
    
    h3 {
      font-size: 1.5rem;
      font-weight: 600;
      color: var(--text-primary);
    }
  }
  
  .battle-characters {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 50px;
    gap: 50px;
    flex-wrap: wrap;
    position: relative;
    z-index: 1;
    
    .battle-character {
      flex: 1;
      min-width: 280px;
      max-width: 340px;
      padding: 24px 20px;
      background: var(--bg-secondary);
      border: 1px solid var(--border-color);
      border-radius: 12px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
      transition: all 0.3s ease;
      position: relative;
      overflow: visible;
      
      // ë°ë¯¸ì§€ ë°›ì„ ë•Œ íš¨ê³¼
      &.taking-damage {
        animation: damageShakeStrong 0.5s ease-in-out;
        
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(255, 0, 0, 0.3);
          border-radius: 12px;
          animation: damageFlash 0.5s ease-out;
          pointer-events: none;
          z-index: 10;
        }
      }
      
      &.player {
        &.active {
          border-color: #3b82f6;
          box-shadow: 0 4px 20px rgba(59, 130, 246, 0.3),
                      inset 0 0 0 1px rgba(59, 130, 246, 0.2);
        }
      }
      
      &.enemy {
        &.active {
          border-color: #ef4444;
          box-shadow: 0 4px 20px rgba(239, 68, 68, 0.3),
                      inset 0 0 0 1px rgba(239, 68, 68, 0.2);
        }
      }
      
      .character-avatar {
        display: none;
      }
      
      .character-name {
        font-size: 1.3rem;
        font-weight: 600;
        margin-bottom: 20px;
        text-align: center;
        color: var(--text-primary);
        letter-spacing: 0.5px;
      }
      
      .character-stats {
        margin-bottom: 16px;
        
        .stat-row {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 12px;
          font-size: 0.95rem;
          color: var(--text-secondary);
          
          .stat-label {
            font-weight: 600;
          }
          
          .stat-value {
            font-weight: 500;
            color: var(--text-primary);
          }
        }
      }
      
      .hp-bar, .mp-bar {
        margin-bottom: 12px;
        position: relative;
        background: transparent;
        
        .bar-label {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 8px;
          background: transparent;
          
          .bar-name {
            font-size: 0.75rem;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 1.5px;
            padding: 2px 8px;
            border-radius: 4px;
            background: transparent;
            color: var(--accent-primary);
          }
          
          .bar-text {
            font-size: 0.85rem;
            color: var(--text-primary);
            font-weight: 700;
            font-family: 'Courier New', monospace;
          }
        }
        
        .bar-container {
          height: 20px;
          background: transparent;
          border-radius: 10px;
          overflow: hidden;
          border: 2px solid rgba(255, 255, 255, 0.1);
          box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.3),
                      0 2px 8px rgba(0, 0, 0, 0.1);
          position: relative;
          
          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 50%;
            background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), transparent);
            border-radius: 10px 10px 0 0;
            pointer-events: none;
            z-index: 2;
          }
          
          .bar-fill {
            height: 100%;
            transition: width 0.6s cubic-bezier(0.34, 1.56, 0.64, 1);
            border-radius: 8px;
            position: relative;
            overflow: hidden;
            box-shadow: inset 0 -2px 4px rgba(0, 0, 0, 0.3);
            
            &::before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              height: 40%;
              background: linear-gradient(to bottom, rgba(255, 255, 255, 0.4), transparent);
              border-radius: 8px 8px 0 0;
              z-index: 1;
            }
            
            &.hp {
              background: linear-gradient(135deg, #ef4444 0%, #dc2626 50%, #ef4444 100%);
              box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2),
                          inset 0 -1px 0 rgba(0, 0, 0, 0.1);
              
              &.low {
                background: linear-gradient(135deg, #dc2626 0%, #b91c1c 50%, #dc2626 100%);
                animation: hpPulseLow 0.8s ease-in-out infinite;
              }
            }
            
            &.mp {
              background: linear-gradient(135deg, #3b82f6 0%, #2563eb 50%, #3b82f6 100%);
              box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2),
                          inset 0 -1px 0 rgba(0, 0, 0, 0.1);
            }
          }
        }
      }
    }
    
    .vs-indicator {
      font-size: 2rem;
      font-weight: 700;
      color: var(--text-secondary);
      opacity: 0.6;
    }
  }
  
  .battle-log {
    max-height: 280px;
    overflow-y: auto;
    padding: 16px;
    background: var(--bg-secondary);
    border-radius: 8px;
    border: 1px solid var(--border-color);
    margin-bottom: 24px;
    font-family: 'Inter', -apple-system, sans-serif;
    scroll-behavior: smooth;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05);
    
    &::-webkit-scrollbar {
      width: 6px;
    }
    
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    
    &::-webkit-scrollbar-thumb {
      background: rgba(0, 0, 0, 0.2);
      border-radius: 3px;
      
      &:hover {
        background: rgba(0, 0, 0, 0.3);
      }
    }
    
    .log-entry {
      padding: 6px 10px;
      color: var(--text-secondary);
      font-size: 0.9rem;
      line-height: 1.5;
      border-radius: 4px;
      margin-bottom: 2px;
      animation: fadeInUp 0.3s ease-out;
      
      &:last-child {
        border-bottom: none;
      }
      
      &.damage {
        color: #ef4444;
        font-weight: 600;
        background: rgba(239, 68, 68, 0.08);
      }
      
      &.heal {
        color: #10b981;
        font-weight: 600;
        background: rgba(16, 185, 129, 0.08);
      }
      
      &.victory {
        color: #fbbf24;
        font-weight: 700;
        font-size: 1rem;
        background: rgba(251, 191, 36, 0.1);
        padding: 8px;
        text-align: center;
        border-radius: 6px;
      }
      
      &.defeat {
        color: #6b7280;
        font-weight: 700;
        font-size: 1rem;
        background: rgba(107, 114, 128, 0.1);
        padding: 8px;
        text-align: center;
        border-radius: 6px;
        opacity: 0.8;
      }
    }
  }
  
  .battle-actions {
    display: flex;
    gap: 12px;
    justify-content: center;
    flex-wrap: wrap;
    
    .action-btn {
      padding: 10px 20px;
      font-size: 0.95rem;
      font-weight: 600;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      color: white;
      
      &.attack {
        background: #ef4444;
        
        &:hover:not(:disabled) {
          background: #dc2626;
        }
      }
      
      &.skill {
        background: #3b82f6;
        
        &:hover:not(:disabled) {
          background: #2563eb;
        }
      }
      
      &.defend {
        background: #10b981;
        
        &:hover:not(:disabled) {
          background: #059669;
        }
      }
      
      &.potion {
        background: #8b5cf6;
        
        &:hover:not(:disabled) {
          background: #7c3aed;
        }
      }
      
      &:disabled {
        opacity: 0.4;
        cursor: not-allowed;
      }
      
      &:active:not(:disabled) {
        transform: scale(0.95);
      }
    }
  }
  
  .battle-result {
    text-align: center;
    padding: 32px 24px;
    background: var(--bg-secondary);
    border-radius: 12px;
    border: 1px solid var(--border-color);
    margin-top: 16px;
    
    h2, h3 {
      font-size: 1.8rem;
      font-weight: 700;
      margin-bottom: 16px;
      
      &.victory {
        color: #fbbf24;
      }
      
      &.defeat {
        color: #6b7280;
      }
    }
    
    h3 {
      font-size: 1.6rem;
    }
    
    p {
      font-size: 0.95rem;
      color: var(--text-secondary);
      margin-bottom: 12px;
      line-height: 1.6;
    }
    
    .auto-continue {
      font-size: 0.9rem;
      color: var(--text-secondary);
      opacity: 0.7;
      margin-top: 16px;
    }
    
    .end-battle-btn {
      margin-top: 20px;
      padding: 10px 24px;
      font-size: 1rem;
      font-weight: 600;
      background: var(--accent-primary);
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      
      &:hover {
        background: var(--accent-secondary);
      }
      
      &:active {
        transform: scale(0.95);
      }
    }
  }
}

