// ê¸°ë³¸ ì• ë‹ˆë©”ì´ì…˜
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes aiGlow {
  0%, 100% {
    filter: drop-shadow(0 0 8px rgba(139, 92, 246, 0.5));
  }
  50% {
    filter: drop-shadow(0 0 16px rgba(236, 72, 153, 0.8));
  }
}

@keyframes swordGlow {
  0%, 100% {
    filter: drop-shadow(0 0 8px rgba(212, 175, 55, 0.5));
    transform: rotate(0deg);
  }
  50% {
    filter: drop-shadow(0 0 16px rgba(212, 175, 55, 0.8));
    transform: rotate(5deg);
  }
}

// ì „íˆ¬ ë°°ê²½ ì• ë‹ˆë©”ì´ì…˜
@keyframes battlePulse {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.5;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 0.3;
  }
}

// ìºë¦­í„° ì• ë‹ˆë©”ì´ì…˜
@keyframes playerGlow {
  0%, 100% {
    box-shadow: 0 15px 40px rgba(59, 130, 246, 0.6),
                0 0 30px rgba(59, 130, 246, 0.4);
  }
  50% {
    box-shadow: 0 15px 40px rgba(59, 130, 246, 0.8),
                0 0 40px rgba(59, 130, 246, 0.6);
  }
}

@keyframes enemyGlow {
  0%, 100% {
    box-shadow: 0 15px 40px rgba(239, 68, 68, 0.6),
                0 0 30px rgba(239, 68, 68, 0.4);
  }
  50% {
    box-shadow: 0 15px 40px rgba(239, 68, 68, 0.8),
                0 0 40px rgba(239, 68, 68, 0.6);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

// HP/MP ë°” ì• ë‹ˆë©”ì´ì…˜
@keyframes hpPulseLow {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

@keyframes shimmerFlow {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(200%);
  }
}

// ë°ë¯¸ì§€ íš¨ê³¼ ì• ë‹ˆë©”ì´ì…˜
@keyframes damageShakeStrong {
  0%, 100% { transform: translateX(0) rotate(0deg); }
  10% { transform: translateX(-10px) rotate(-2deg); }
  20% { transform: translateX(10px) rotate(2deg); }
  30% { transform: translateX(-10px) rotate(-2deg); }
  40% { transform: translateX(10px) rotate(2deg); }
  50% { transform: translateX(-8px) rotate(-1deg); }
  60% { transform: translateX(8px) rotate(1deg); }
  70% { transform: translateX(-5px) rotate(-0.5deg); }
  80% { transform: translateX(5px) rotate(0.5deg); }
  90% { transform: translateX(-2px) rotate(0deg); }
}

@keyframes damageFlash {
  0% { opacity: 0; }
  10% { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes damageNumber {
  0% {
    transform: translateY(0) scale(0.5);
    opacity: 0;
  }
  20% {
    transform: translateY(-20px) scale(1.2);
    opacity: 1;
  }
  80% {
    transform: translateY(-60px) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateY(-80px) scale(0.8);
    opacity: 0;
  }
}

// VS ì¸ë””ì¼€ì´í„° ì• ë‹ˆë©”ì´ì…˜
@keyframes vsGlow {
  0%, 100% {
    filter: drop-shadow(0 0 20px rgba(59, 130, 246, 0.6));
    transform: scale(1);
  }
  50% {
    filter: drop-shadow(0 0 30px rgba(59, 130, 246, 0.9));
    transform: scale(1.1);
  }
}

@keyframes sparkle {
  0%, 100% {
    opacity: 0.5;
    transform: rotate(0deg) scale(1);
  }
  50% {
    opacity: 1;
    transform: rotate(180deg) scale(1.2);
  }
}

// ë¡œê·¸ ì• ë‹ˆë©”ì´ì…˜
@keyframes damageShake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-3px);
  }
  75% {
    transform: translateX(3px);
  }
}

@keyframes healPulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 0 rgba(16, 185, 129, 0);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 10px rgba(16, 185, 129, 0.5);
  }
}

@keyframes victoryFlash {
  0%, 100% {
    opacity: 1;
    box-shadow: 0 0 10px rgba(251, 191, 36, 0.5);
  }
  50% {
    opacity: 0.8;
    box-shadow: 0 0 20px rgba(251, 191, 36, 0.8);
  }
}

// ê²°ê³¼ í™”ë©´ ì• ë‹ˆë©”ì´ì…˜
@keyframes resultGlow {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.6;
  }
}

@keyframes victoryBounce {
  0%, 100% {
    transform: translateY(0) scale(1);
  }
  50% {
    transform: translateY(-10px) scale(1.05);
  }
}

@keyframes celebrate {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-15px) rotate(360deg);
  }
}

@keyframes blink {
