    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    cursor: pointer;
    background: var(--bg-primary);
    transition: background 0.2s ease;

    &:hover {
      background: var(--bg-secondary);
    }

    .header-left {
      display: flex;
      align-items: center;
      gap: 16px;
      flex: 1;

      .expand-icon {
        font-size: 14px;
        color: #667eea;
        transition: transform 0.2s ease;
        min-width: 20px;
      }

      h3 {
        font-size: 20px;
        font-weight: 700;
        color: var(--text-primary);
        margin: 0;
      }

      .count-badge {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        min-width: 32px;
        height: 28px;
        padding: 0 12px;
        background: #667eea;
        color: white;
        border-radius: 14px;
        font-size: 13px;
        font-weight: 700;
