      .gold-badge {
        display: inline-flex;
        align-items: center;
        gap: 6px;
        padding: 6px 12px;
        background: #f59e0b;
        color: white;
        border-radius: 10px;
        font-size: 14px;
        font-weight: 700;
      }
    }

    .toggle-btn {
      padding: 8px 16px;
      background: var(--bg-secondary);
      color: #667eea;
      border: 2px solid #667eea;
      border-radius: 8px;
      font-size: 13px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.2s ease;

      &:hover {
        background: #667eea;
        color: white;
      }

      &:active {
        transform: scale(0.98);
      }
    }
  }

  .section-content {
    padding: 24px;
    animation: slideDown 0.3s ease;
    background: var(--bg-primary);

    .section-desc {
      text-align: center;
      color: var(--text-secondary);
      font-size: 14px;
      margin-bottom: 24px;
