.flow-canvas-container {
  flex: 1;
  position: relative;
  overflow: hidden;
  background: linear-gradient(180deg, 
    var(--bg-primary) 0%, 
    var(--bg-secondary) 100%);
  cursor: grab;
  user-select: none;
  
  &:active {
    cursor: grabbing;
  }
}

.flow-svg-canvas {
  width: 100%;
  height: 100%;
  
  .connection-path {
    fill: none;
    stroke: #cbd5e0;
    stroke-width: 2;
    opacity: 0.6;
    transition: all 0.3s ease;
    
    &.visited {
      stroke: #48bb78;
      stroke-width: 3;
      opacity: 0.9;
    }
    
    &.current {
      stroke: #4299e1;
      stroke-width: 4;
      opacity: 1;
      animation: pathPulse 2s infinite;
    }
    
    &:hover {
      stroke-width: 4;
      opacity: 1;
    }
  }
  
  .story-node {
    cursor: pointer;
    transition: all 0.3s ease;
    
    &:hover {
      .node-circle {
        filter: brightness(1.1);
        transform: scale(1.1);
      }
    }
    
    &.selected {
      .node-selection-ring {
        stroke: #fbbf24;
        stroke-width: 3;
        opacity: 0.8;
      }
    }
    
    &.dragging {
      cursor: move !important;
      opacity: 0.8;
      
      .node-circle {
        filter: drop-shadow(0 0 15px rgba(251, 191, 36, 0.6));
        transform: scale(1.15);
      }
    }
    
    &.current {
      .node-circle {
        filter: drop-shadow(0 0 20px #4299e1);
        animation: nodeGlow 2s infinite;
      }
      
      .node-glow {
        animation: glowPulse 2s infinite;
      }
    }
    
    &.visited {
      .node-circle {
        filter: drop-shadow(0 0 8px rgba(72, 187, 120, 0.5));
      }
    }
    
    &.battle {
      .node-circle {
        filter: drop-shadow(0 0 10px rgba(252, 129, 129, 0.6));
      }
    }
    
    &.ending {
      .node-circle {
        filter: drop-shadow(0 0 15px rgba(159, 122, 234, 0.8));
      }
    }
    
    .node-circle {
      transition: all 0.3s ease;
    }
    
    .node-selection-ring {
      transition: all 0.3s ease;
    }
    
    .node-icon {
      pointer-events: none;
      user-select: none;
    }
    
    .node-title {
      fill: var(--text-primary);
      pointer-events: none;
    }
    
    .badge-text,
    .visit-count {
      fill: white;
      font-weight: 600;
      pointer-events: none;
    }
  }
}

