    margin: 16px 0 12px;
    font-size: 16px;
    font-weight: 600;
    color: var(--text-primary);
  }
  
  .current-location {
    margin-bottom: 20px;
    
    .location-card {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 16px;
      background: linear-gradient(135deg, rgba(66, 153, 225, 0.1) 0%, rgba(66, 153, 225, 0.05) 100%);
      border: 2px solid #4299e1;
      border-radius: 12px;
      animation: nodeGlow 2s ease-in-out infinite;
      
