        color: var(--text-secondary);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}

// ì• ë‹ˆë©”ì´ì…˜
@keyframes pathPulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

@keyframes nodeGlow {
  0%, 100% {
    filter: drop-shadow(0 0 20px #4299e1);
  }
  50% {
    filter: drop-shadow(0 0 30px #4299e1);
  }
}

@keyframes glowPulse {
  0%, 100% {
    opacity: 0.5;
    r: 50;
  }
  50% {
    opacity: 0.8;
    r: 55;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(40px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

