// í”Œë¡œìš° íŒ¨ë„ í† ê¸€ ë²„íŠ¼ (íŒ¨ë„ì´ ë‹«í˜”ì„ ë•Œ)
.flow-panel-toggle-btn {
  position: fixed;
  top: 80px;
  right: 24px;
  z-index: 899;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 16px;
  padding: 14px 18px;
  cursor: pointer;
  box-shadow: 
    0 8px 24px rgba(102, 126, 234, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  animation: slideInRight 0.4s ease, pulseGlow 3s ease-in-out infinite;
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
  
  // ë°˜ì§ì´ëŠ” íš¨ê³¼
  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(
      45deg,
      transparent 30%,
      rgba(255, 255, 255, 0.3) 50%,
      transparent 70%
    );
    animation: shimmer 3s infinite;
  }
  
  &:hover {
    transform: translateX(-6px) translateY(-2px) scale(1.08);
    box-shadow: 
      0 12px 32px rgba(102, 126, 234, 0.5),
      0 0 0 1px rgba(255, 255, 255, 0.4);
    
    .toggle-icon {
      transform: scale(1.2) rotate(10deg);
    }
  }
  
  &:active {
    transform: translateX(-6px) scale(1.02);
  }
  
  .toggle-icon {
    font-size: 28px;
    line-height: 1;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    z-index: 1;
  }
  
  .toggle-text {
    font-size: 12px;
    font-weight: 800;
    letter-spacing: 1px;
    text-transform: uppercase;
    position: relative;
    z-index: 1;
  }
}

// í† ê¸€ ë²„íŠ¼ ì• ë‹ˆë©”ì´ì…˜
@keyframes shimmer {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(0deg);
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(0deg);
  }
}

@keyframes pulseGlow {
  0%, 100% {
    box-shadow: 
      0 8px 24px rgba(102, 126, 234, 0.4),
      0 0 0 1px rgba(255, 255, 255, 0.2);
  }
  50% {
    box-shadow: 
      0 8px 32px rgba(102, 126, 234, 0.6),
      0 0 0 1px rgba(255, 255, 255, 0.3),
      0 0 20px rgba(102, 126, 234, 0.3);
  }
}

// í„ìŠ¤ ì• ë‹ˆë©”ì´ì…˜
@keyframes phonePulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1);
  }
}

