.epic-battle {
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
  border-radius: 20px;
  box-shadow: 
    0 30px 80px rgba(0, 0, 0, 0.6),
    inset 0 0 80px rgba(138, 43, 226, 0.1);
  
  .battle-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    overflow: hidden;
    
    .battle-particles {
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: 
        radial-gradient(2px 2px at 20% 30%, rgba(255, 255, 255, 0.3), transparent),
        radial-gradient(2px 2px at 60% 70%, rgba(138, 43, 226, 0.3), transparent),
        radial-gradient(1px 1px at 50% 50%, rgba(255, 215, 0, 0.3), transparent),
        radial-gradient(1px 1px at 80% 10%, rgba(195, 20, 50, 0.3), transparent);
      background-size: 200% 200%, 180% 180%, 220% 220%, 190% 190%;
      animation: particleFloat 20s ease-in-out infinite;
    }
    
    .battle-glow {
      position: absolute;
      width: 100%;
      height: 100%;
      background: radial-gradient(
        circle at 50% 50%,
        rgba(138, 43, 226, 0.15) 0%,
        transparent 70%
      );
      animation: glowPulse 4s ease-in-out infinite;
    }
  }
  
  .battle-header {
    position: relative;
    z-index: 1;
    padding: 24px 32px;
    background: linear-gradient(
      135deg,
      rgba(138, 43, 226, 0.3) 0%,
      rgba(195, 20, 50, 0.3) 100%
    );
    border-bottom: 3px solid rgba(255, 215, 0, 0.3);
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    h3 {
      font-size: 2rem;
      font-weight: 800;
      color: white;
      margin: 0;
      display: flex;
      align-items: center;
      gap: 16px;
      text-shadow: 
        0 0 20px rgba(255, 215, 0, 0.6),
        0 0 40px rgba(138, 43, 226, 0.4);
      animation: titlePulse 2s ease-in-out infinite;
      
      .battle-title-icon {
        animation: iconSpin 3s linear infinite;
      }
    }
    
    .turn-indicator {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 12px 24px;
      background: rgba(0, 0, 0, 0.4);
      border-radius: 12px;
      border: 2px solid rgba(255, 215, 0, 0.3);
      
      .turn-label {
        color: rgba(255, 255, 255, 0.7);
        font-size: 1rem;
        font-weight: 600;
      }
      
      .turn-value {
        font-size: 1.2rem;
        font-weight: 800;
        padding: 4px 12px;
        border-radius: 6px;
        transition: all 0.3s ease;
        
        &.player-turn {
          color: #64c8ff;
          background: rgba(100, 200, 255, 0.2);
          box-shadow: 0 0 20px rgba(100, 200, 255, 0.4);
          animation: turnGlow 1s ease-in-out infinite;
        }
        
        &.enemy-turn {
          color: #ff6464;
          background: rgba(255, 100, 100, 0.2);
          box-shadow: 0 0 20px rgba(255, 100, 100, 0.4);
          animation: turnGlow 1s ease-in-out infinite;
        }
      }
    }
  }
  
  .battle-arena {
    position: relative;
    z-index: 1;
    padding: 40px 20px;
  }
  
  .battle-characters {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 40px;
    max-width: 1200px;
    margin: 0 auto;
    
    .battle-character {
      flex: 1;
      max-width: 400px;
      position: relative;
      transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      
      &.active {
        transform: scale(1.05);
        
        .character-glow {
          opacity: 1;
          animation: activeGlow 1.5s ease-in-out infinite;
        }
      }
      
      &.taking-damage {
        animation: shakeDamage 0.5s ease-in-out;
      }
      
      &.attacking {
        animation: attackPulse 0.8s ease-out;
      }
      
      .character-glow {
        position: absolute;
        top: -20px;
        left: -20px;
        right: -20px;
        bottom: -20px;
        background: radial-gradient(
          circle,
          rgba(100, 200, 255, 0.3) 0%,
          transparent 70%
        );
        border-radius: 50%;
        opacity: 0;
        transition: opacity 0.5s ease;
        pointer-events: none;
        
        &.enemy-glow {
          background: radial-gradient(
            circle,
            rgba(255, 100, 100, 0.3) 0%,
            transparent 70%
          );
        }
      }
      
      .character-card {
        background: linear-gradient(
          135deg,
          rgba(30, 30, 60, 0.9) 0%,
          rgba(20, 20, 40, 0.9) 100%
        );
        border-radius: 20px;
        padding: 24px;
        border: 3px solid rgba(100, 200, 255, 0.3);
        box-shadow: 
          0 20px 60px rgba(0, 0, 0, 0.5),
          inset 0 0 40px rgba(100, 200, 255, 0.05);
        position: relative;
        overflow: hidden;
        
        &::before {
          content: '';
          position: absolute;
          top: -50%;
          left: -50%;
          width: 200%;
          height: 200%;
          background: linear-gradient(
            45deg,
            transparent 30%,
            rgba(255, 255, 255, 0.05) 50%,
            transparent 70%
          );
          animation: cardShine 3s ease-in-out infinite;
        }
        
        &.enemy-card {
          border-color: rgba(255, 100, 100, 0.3);
          box-shadow: 
            0 20px 60px rgba(0, 0, 0, 0.5),
            inset 0 0 40px rgba(255, 100, 100, 0.05);
        }
      }
      
      .character-avatar-container {
        position: relative;
        width: 120px;
        height: 120px;
        margin: 0 auto 16px;
        
        .avatar-ring {
          position: absolute;
          top: -10px;
          left: -10px;
          right: -10px;
          bottom: -10px;
          border: 3px solid rgba(100, 200, 255, 0.5);
          border-radius: 50%;
          animation: ringRotate 4s linear infinite;
          
          &::before,
          &::after {
            content: '';
            position: absolute;
            width: 10px;
            height: 10px;
            background: rgba(100, 200, 255, 1);
            border-radius: 50%;
            box-shadow: 0 0 15px rgba(100, 200, 255, 0.8);
          }
          
          &::before {
            top: 0;
            left: 50%;
            transform: translateX(-50%);
          }
          
          &::after {
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
          }
          
          &.enemy-ring {
            border-color: rgba(255, 100, 100, 0.5);
            
            &::before,
            &::after {
              background: rgba(255, 100, 100, 1);
              box-shadow: 0 0 15px rgba(255, 100, 100, 0.8);
            }
          }
        }
        
        .character-avatar {
