      }
      
      .stats-container {
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-bottom: 16px;
      }
      
      .hp-bar,
      .mp-bar,
      .internal-power-bar {
        .bar-label {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 6px;
          
          .bar-icon {
            font-size: 1.2rem;
            filter: drop-shadow(0 0 5px currentColor);
          }
          
          .bar-name {
            font-weight: 700;
            font-size: 1rem;
            margin-left: 8px;
          }
          
          .bar-text {
            font-size: 0.95rem;
            font-weight: 700;
            color: rgba(255, 255, 255, 0.9);
          }
        }
        
        .bar-container {
          height: 16px;
          background: rgba(0, 0, 0, 0.6);
          border-radius: 10px;
          overflow: hidden;
          border: 2px solid rgba(255, 255, 255, 0.2);
          position: relative;
          box-shadow: inset 0 2px 8px rgba(0, 0, 0, 0.5);
          
          .bar-fill {
            height: 100%;
            position: relative;
            transition: width 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            
            .bar-shine {
              position: absolute;
              top: 0;
              left: -100%;
              width: 100%;
              height: 100%;
              background: linear-gradient(
                90deg,
                transparent 0%,
                rgba(255, 255, 255, 0.4) 50%,
                transparent 100%
              );
              animation: barShine 2s ease-in-out infinite;
            }
            
            &.hp {
              background: linear-gradient(90deg, #ff6b6b 0%, #ee5a6f 100%);
              box-shadow: 0 0 15px rgba(255, 107, 107, 0.5);
              
              &.low {
                animation: hpWarning 1s ease-in-out infinite;
              }
              
              &.critical {
                animation: hpCritical 0.5s ease-in-out infinite;
                box-shadow: 0 0 25px rgba(255, 0, 0, 0.8);
              }
              
              &.enemy-hp {
                background: linear-gradient(90deg, #c31432 0%, #8e0000 100%);
              }
            }
            
            &.mp {
              background: linear-gradient(90deg, #667eea 0%, #764ba2 100%);
              box-shadow: 0 0 15px rgba(102, 126, 234, 0.5);
            }
            
            &.internal-power {
              background: linear-gradient(90deg, #ff6b6b 0%, #ffd700 50%, #ff6b6b 100%);
              background-size: 200% 100%;
              box-shadow: 0 0 15px rgba(255, 215, 0, 0.6);
              animation: powerFlow 3s ease-in-out infinite;
            }
          }
        }
      }
      
      .character-stats-row {
        display: flex;
        justify-content: space-around;
        gap: 12px;
        margin-top: 16px;
        
        .stat-item {
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          padding: 12px;
          background: rgba(0, 0, 0, 0.3);
          border-radius: 10px;
          border: 2px solid rgba(255, 255, 255, 0.1);
          
          .stat-icon {
            font-size: 1.3rem;
            filter: drop-shadow(0 0 5px currentColor);
          }
          
          .stat-value {
            font-size: 1.2rem;
            font-weight: 800;
            color: white;
            text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
          }
        }
      }
    }
    
    .battle-vs-container {
      position: relative;
      flex-shrink: 0;
      
      .vs-circle {
        width: 100px;
        height: 100px;
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        box-shadow: 
          0 0 40px rgba(102, 126, 234, 0.6),
          inset 0 0 20px rgba(255, 255, 255, 0.2);
        animation: vsRotate 4s linear infinite;
        
        &::before,
        &::after {
          content: '';
          position: absolute;
          border-radius: 50%;
        }
        
        &::before {
          top: -10px;
          left: -10px;
          right: -10px;
          bottom: -10px;
          border: 3px dashed rgba(255, 215, 0, 0.5);
          animation: ringRotate 6s linear infinite reverse;
        }
        
        &::after {
          top: -20px;
          left: -20px;
          right: -20px;
          bottom: -20px;
          border: 2px dotted rgba(255, 255, 255, 0.3);
          animation: ringRotate 8s linear infinite;
        }
        
        .vs-text {
          font-size: 2rem;
          font-weight: 900;
          color: white;
          text-shadow: 
            0 0 20px rgba(255, 215, 0, 1),
            0 0 40px rgba(138, 43, 226, 0.8);
          animation: vsScale 2s ease-in-out infinite;
          z-index: 1;
        }
        
        .vs-lightning {
          position: absolute;
          width: 100%;
          height: 100%;
          
          &::before,
          &::after {
            content: 'âš¡';
            position: absolute;
            font-size: 1.5rem;
            animation: lightningFlash 2s ease-in-out infinite;
          }
          
          &::before {
            top: -10px;
            left: 50%;
            transform: translateX(-50%);
          }
          
          &::after {
            bottom: -10px;
            left: 50%;
            transform: translateX(-50%);
            animation-delay: 1s;
          }
        }
      }
    }
  }
}

.epic-actions {
  position: relative;
  z-index: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 16px;
  padding: 24px 32px;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.3) 0%,
    rgba(0, 0, 0, 0.5) 100%
  );
  border-radius: 16px;
  margin: 20px 32px;
  border: 2px solid rgba(255, 215, 0, 0.2);
  
  .action-btn {
    position: relative;
    padding: 20px 16px;
    background: linear-gradient(135deg, rgba(102, 126, 234, 0.3) 0%, rgba(118, 75, 162, 0.3) 100%);
    border: 3px solid rgba(102, 126, 234, 0.6);
    border-radius: 14px;
    color: white;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    overflow: hidden;
    
    .btn-glow {
      position: absolute;
      top: -50%;
      left: -50%;
      width: 200%;
      height: 200%;
      background: radial-gradient(
        circle,
        rgba(255, 255, 255, 0.2) 0%,
        transparent 70%
      );
      opacity: 0;
      transition: opacity 0.3s ease;
    }
    
    .btn-particles {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: 
        radial-gradient(2px 2px at 20% 30%, rgba(255, 255, 255, 0.5), transparent),
        radial-gradient(2px 2px at 80% 70%, rgba(255, 255, 255, 0.5), transparent);
      background-size: 100% 100%;
      opacity: 0;
      transition: opacity 0.3s ease;
    }
    
    .btn-icon {
      font-size: 2.2rem;
      filter: drop-shadow(0 0 10px currentColor);
      transition: transform 0.3s ease;
      z-index: 1;
    }
    
    .btn-text {
      font-size: 1rem;
      letter-spacing: 1px;
      z-index: 1;
    }
    
    .btn-cost {
      font-size: 0.85rem;
      color: rgba(255, 255, 255, 0.7);
      z-index: 1;
    }
    
    &:hover:not(:disabled) {
      transform: translateY(-5px) scale(1.05);
      box-shadow: 0 15px 40px rgba(102, 126, 234, 0.5);
      
      .btn-glow {
        opacity: 1;
      }
      
      .btn-particles {
        opacity: 1;
        animation: particleRise 2s ease-out infinite;
      }
      
      .btn-icon {
        transform: scale(1.2) rotate(5deg);
      }
    }
    
    &:active:not(:disabled) {
      transform: translateY(-3px) scale(1.02);
    }
    
    &:disabled {
      opacity: 0.4;
      cursor: not-allowed;
      filter: grayscale(0.7);
      transform: scale(0.95);
    }
    
    &.attack-btn {
      border-color: rgba(255, 100, 100, 0.6);
      background: linear-gradient(135deg, rgba(255, 100, 100, 0.3) 0%, rgba(195, 20, 50, 0.3) 100%);
      
      &:hover:not(:disabled) {
        box-shadow: 0 15px 40px rgba(255, 100, 100, 0.6);
      }
    }
    
    &.skill-btn {
      border-color: rgba(138, 43, 226, 0.6);
      background: linear-gradient(135deg, rgba(138, 43, 226, 0.3) 0%, rgba(102, 126, 234, 0.3) 100%);
      
      &:hover:not(:disabled) {
        box-shadow: 0 15px 40px rgba(138, 43, 226, 0.6);
      }
    }
    
    &.defend-btn {
      border-color: rgba(100, 200, 255, 0.6);
      background: linear-gradient(135deg, rgba(100, 200, 255, 0.3) 0%, rgba(64, 156, 255, 0.3) 100%);
      
      &:hover:not(:disabled) {
        box-shadow: 0 15px 40px rgba(100, 200, 255, 0.6);
      }
    }
    
    &.potion-btn {
      border-color: rgba(50, 205, 50, 0.6);
      background: linear-gradient(135deg, rgba(50, 205, 50, 0.3) 0%, rgba(34, 139, 34, 0.3) 100%);
      
      &:hover:not(:disabled) {
        box-shadow: 0 15px 40px rgba(50, 205, 50, 0.6);
      }
    }
  }
}

