.epic-result {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: resultFadeIn 0.5s ease-out;
  
  .result-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(10px);
  }
  
  .result-content {
    position: relative;
    z-index: 1;
    text-align: center;
    padding: 60px 80px;
    background: linear-gradient(
      135deg,
      rgba(30, 30, 60, 0.95) 0%,
      rgba(20, 20, 40, 0.95) 100%
    );
