    border: 4px solid rgba(255, 215, 0, 0.5);
    box-shadow: 
      0 40px 100px rgba(0, 0, 0, 0.8),
      inset 0 0 60px rgba(255, 215, 0, 0.1);
    animation: resultScale 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    
