.flat-battle {
  margin: 30px 0;
  padding: 0;
  background: transparent;
  border: none;
  box-shadow: none;
  
  .battle-container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .battle-title {
    text-align: center;
    margin-bottom: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(150, 150, 150, 0.3);
    
    // ëª¨ë°”ì¼ ìµœì í™”
    @media (max-width: 768px) {
      margin-bottom: 20px;
      padding-bottom: 16px;
    }
    
    @media (max-width: 480px) {
      margin-bottom: 16px;
      padding-bottom: 12px;
    }
    
    h3 {
      font-size: 1.4rem;
      font-weight: 400;
      color: rgba(0, 0, 0, 0.8);
      margin: 0 0 10px 0;
      letter-spacing: 8px;
      
      // ëª¨ë°”ì¼ ìµœì í™”
      @media (max-width: 768px) {
        font-size: 1.2rem;
        letter-spacing: 6px;
      }
      
      @media (max-width: 480px) {
        font-size: 1.1rem;
        letter-spacing: 4px;
      }
    }
    
    .turn-text {
      font-size: 0.95rem;
      color: rgba(100, 100, 100, 0.8);
      font-style: italic;
      margin: 0;
      
      // ëª¨ë°”ì¼ ìµœì í™”
      @media (max-width: 768px) {
        font-size: 0.85rem;
      }
      
      @media (max-width: 480px) {
        font-size: 0.8rem;
      }
    }
  }
  
  .battle-board {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    gap: 30px;
    align-items: start;
    
    // ëª¨ë°”ì¼ ìµœì í™”
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 16px;
      
      .vs-divider {
        order: 2;
        padding: 12px 0;
        font-size: 1.2rem;
      }
      
      .player-side {
        order: 1;
      }
      
      .enemy-side {
        order: 3;
      }
    }
    
    .combatant {
      background: rgba(250, 250, 250, 0.8);
      border: 1px solid rgba(200, 200, 200, 0.4);
      border-radius: 4px;
      padding: 20px;
      transition: all 0.3s ease;
      
      &.active-turn {
        border-color: rgba(100, 100, 100, 0.6);
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      }
      
      // ëª¨ë°”ì¼ ìµœì í™”
      @media (max-width: 768px) {
        padding: 16px;
      }
      
      @media (max-width: 480px) {
        padding: 12px;
      }
      
      .combatant-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 16px;
        padding-bottom: 12px;
        border-bottom: 1px solid rgba(200, 200, 200, 0.3);
        
        h4 {
          font-size: 1.1rem;
          font-weight: 600;
          color: rgba(0, 0, 0, 0.85);
          margin: 0;
        }
        
        .level-tag {
          font-size: 0.75rem;
          color: rgba(100, 100, 100, 0.7);
          padding: 2px 8px;
          background: rgba(200, 200, 200, 0.3);
          border-radius: 3px;
        }
        
        // ëª¨ë°”ì¼ ìµœì í™”
        @media (max-width: 768px) {
          margin-bottom: 12px;
          padding-bottom: 10px;
          
          h4 {
            font-size: 1rem;
          }
          
          .level-tag {
            font-size: 0.7rem;
            padding: 2px 6px;
          }
        }
      }
      
      .stats-list {
        display: flex;
        flex-direction: column;
        gap: 12px;
        
        // ëª¨ë°”ì¼ ìµœì í™”
        @media (max-width: 768px) {
          gap: 10px;
        }
        
        .stat-row {
          display: flex;
          align-items: center;
          gap: 10px;
          
          &.compact {
            margin-top: 8px;
            padding-top: 12px;
            border-top: 1px solid rgba(200, 200, 200, 0.2);
          }
          
          .stat-label {
            font-size: 0.85rem;
            color: rgba(100, 100, 100, 0.8);
            min-width: 40px;
            font-weight: 500;
            
            // ëª¨ë°”ì¼ ìµœì í™”
            @media (max-width: 768px) {
              font-size: 0.8rem;
              min-width: 36px;
            }
            
            @media (max-width: 480px) {
              font-size: 0.75rem;
              min-width: 32px;
            }
          }
          
          .stat-bar-wrapper {
            flex: 1;
            display: flex;
            align-items: center;
            gap: 10px;
            
            // ëª¨ë°”ì¼ ìµœì í™”
            @media (max-width: 768px) {
              gap: 8px;
            }
            
            .stat-bar {
              flex: 1;
              height: 8px;
              background: rgba(220, 220, 220, 0.6);
              border-radius: 4px;
              overflow: hidden;
              
              // ëª¨ë°”ì¼ ìµœì í™” - ë” í° ë°”
              @media (max-width: 768px) {
                height: 10px;
              }
              
              @media (max-width: 480px) {
                height: 12px;
              }
              
              .stat-bar-fill {
                height: 100%;
                transition: width 0.3s ease;
                border-radius: 4px;
                
                &.hp {
                  background: #e57373;
                  
                  &.low {
                    background: #ef5350;
                  }
                }
                
                &.enemy-hp {
                  background: #ba68c8;
                  
                  &.low {
                    background: #ab47bc;
                  }
                }
                
                &.mp {
                  background: #64b5f6;
                }
                
                &.internal {
                  background: #ffb74d;
                }
              }
            }
            
            .stat-value {
              font-size: 0.8rem;
              color: rgba(100, 100, 100, 0.8);
              min-width: 60px;
              text-align: right;
              font-variant-numeric: tabular-nums;
            }
          }
          
          .stat-value-inline {
            font-size: 0.85rem;
            color: rgba(50, 50, 50, 0.9);
            font-weight: 600;
            font-variant-numeric: tabular-nums;
          }
          
          .stat-separator {
            color: rgba(150, 150, 150, 0.5);
            margin: 0 6px;
          }
        }
      }
    }
    
    .vs-divider {
      font-size: 0.9rem;
      font-weight: 300;
      color: rgba(150, 150, 150, 0.6);
      text-align: center;
      padding-top: 50px;
      letter-spacing: 2px;
    }
  }
}

