    border-width: 3px;
    background: linear-gradient(180deg, 
      rgba(249, 115, 22, 0.15) 0%, 
      rgba(10, 10, 15, 0.98) 100%
    );
    
    &::before {
      content: '';
      position: absolute;
      top: -3px;
      left: -3px;
      right: -3px;
      bottom: -3px;
      border-radius: 4px;
      background: linear-gradient(45deg, 
        #f97316 0%, 
        #fb923c 20%, 
        #fbbf24 40%, 
        #fb923c 60%, 
        #f97316 80%, 
        #fb923c 100%
      );
      z-index: -1;
      opacity: 0.6;
      filter: blur(12px);
      animation: legendary-glow 3s ease-in-out infinite;
    }
    
    &::after {
      height: 60%;
      background: linear-gradient(180deg, 
        transparent 0%, 
        rgba(249, 115, 22, 0.3) 50%,
        rgba(0, 0, 0, 0.6) 100%
