        #f472b6 100%
      );
      z-index: -1;
      opacity: 0.7;
      filter: blur(14px);
      animation: legendary-glow 3s ease-in-out infinite;
    }
    
    &:hover {
      border-color: #f472b6;
      transform: translateY(-5px) scale(1.02);
      box-shadow: 
        0 15px 30px rgba(236, 72, 153, 0.9),
        0 8px 16px rgba(0, 0, 0, 0.6),
        0 0 50px rgba(236, 72, 153, 0.8),
        inset 0 0 40px rgba(236, 72, 153, 0.3);
        
      &::before {
        opacity: 1;
        filter: blur(18px);
      }
    }
    
    .item-name {
      color: #fbcfe8;
      text-shadow: 
        0 2px 6px rgba(0, 0, 0, 1),
        0 0 30px rgba(236, 72, 153, 1),
        0 0 20px rgba(244, 114, 182, 1),
        0 0 12px rgba(249, 168, 212, 0.9);
      letter-spacing: 2px;
      font-weight: 900;
      text-transform: uppercase;
      animation: legendary-text 2s ease-in-out infinite;
    }
    
    .item-header {
      background: linear-gradient(180deg, 
        rgba(236, 72, 153, 0.45) 0%, 
        rgba(236, 72, 153, 0.18) 100%
      );
      border-bottom: 2px solid rgba(236, 72, 153, 0.85);
    }
  }
}

