      }
      
      @media (max-width: 480px) {
        width: 48px;
        height: 48px;
        font-size: 28px;
      }
      
      &:hover {
        background: #ef4444;
        border-color: #ef4444;
        color: white;
      }
    }
  }
  
  .modal-body {
    padding: 20px;
    cursor: default;
    
    @media (max-width: 768px) {
      padding: 16px;
    }
    
    @media (max-width: 480px) {
      padding: 20px;
    }
    
    .item-info-header {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 16px;
      border-radius: 8px;
      background: var(--bg-secondary);
      margin-bottom: 16px;
      border: 2px solid var(--border-color);
      
      @media (max-width: 768px) {
        padding: 18px;
        gap: 14px;
      }
      
      @media (max-width: 480px) {
        padding: 20px;
      }
      
      &.rarity-common { border-color: #9ca3af; }
      &.rarity-uncommon { border-color: #10b981; }
      &.rarity-rare { border-color: #3b82f6; }
      &.rarity-epic { border-color: #a855f7; }
      &.rarity-legendary { border-color: #f97316; }
      &.rarity-unique { border-color: #ec4899; }
      &.rarity-mythic { border-color: #fbbf24; }
      
      .item-info-icon {
        font-size: 48px;
        width: 64px;
        height: 64px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bg-tertiary);
        border-radius: 8px;
        flex-shrink: 0;
        
        @media (max-width: 768px) {
          font-size: 52px;
          width: 72px;
          height: 72px;
        }
        
        @media (max-width: 480px) {
          font-size: 56px;
          width: 80px;
          height: 80px;
        }
      }
      
      .item-info-title {
        flex: 1;
        
        h3 {
          margin: 0 0 8px 0;
          font-size: 22px;
          font-weight: 700;
          color: var(--text-primary);
        }
        
        .item-rarity-badge {
          display: inline-block;
          padding: 4px 12px;
          border-radius: 12px;
          font-size: 12px;
          font-weight: 600;
          background: var(--accent-primary);
          color: white;
        }
      }
    }
    
    .item-info-meta {
      display: flex;
      gap: 8px;
      margin-bottom: 16px;
      
      .meta-badge {
        padding: 6px 12px;
        border-radius: 6px;
        font-size: 13px;
        font-weight: 500;
        
        &.type-badge {
          background: var(--bg-secondary);
          color: var(--accent-primary);
          border: 1px solid var(--accent-primary);
        }
        
        &.quantity-badge {
          background: var(--accent-primary);
          color: white;
        }
      }
    }
    
    .item-info-stats {
      background: var(--bg-secondary);
      padding: 16px;
      border-radius: 8px;
      margin-bottom: 16px;
      
      .stats-title {
        margin: 0 0 12px 0;
        font-size: 14px;
        font-weight: 700;
        color: var(--accent-primary);
        text-transform: uppercase;
        letter-spacing: 1px;
      }
      
      .stats-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 12px;
        
        .stat-item {
          display: flex;
          align-items: center;
          gap: 8px;
          padding: 10px;
          background: var(--bg-primary);
          border-radius: 6px;
          border: 1px solid var(--border-color);
          
          .stat-icon {
            font-size: 20px;
            width: 28px;
            text-align: center;
          }
          
          .stat-label {
            flex: 1;
            font-size: 13px;
            color: var(--text-secondary);
          }
          
          .stat-value {
            font-size: 15px;
            font-weight: 700;
            color: var(--accent-primary);
          }
        }
      }
    }
    
    .item-info-description {
      background: var(--bg-secondary);
      padding: 16px;
      border-radius: 8px;
      margin-bottom: 16px;
      
      .desc-title {
        margin: 0 0 8px 0;
        font-size: 14px;
        font-weight: 700;
        color: var(--accent-primary);
        text-transform: uppercase;
        letter-spacing: 1px;
      }
      
      p {
        margin: 0;
        font-size: 14px;
        line-height: 1.6;
        color: var(--text-secondary);
      }
    }
    
    .item-info-actions {
      display: flex;
      gap: 12px;
      
      .action-btn {
        flex: 1;
        padding: 12px 20px;
        border-radius: 8px;
        font-size: 15px;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.2s ease;
        border: 2px solid;
        
        &.equip-btn {
          background: var(--accent-primary);
          border-color: var(--accent-primary);
          color: white;
          
          &:hover {
            background: transparent;
            color: var(--accent-primary);
          }
        }
        
        &.discard-btn {
          background: transparent;
          border-color: #ef4444;
          color: #ef4444;
          
          &:hover {
            background: #ef4444;
            color: white;
          }
        }
      }
    }
  }
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

