.character-inventory-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 20px;
  animation: fadeIn 0.2s ease;
  
  .character-inventory-modal {
    background: var(--bg-primary);
    border-radius: 20px;
    width: 100%;
    max-width: 1000px;
    max-height: 90vh;
    display: flex;
    flex-direction: column;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
    border: 2px solid var(--border-color);
    animation: modalSlideIn 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
    overflow: hidden;
    
    // í—¤ë” ìŠ¤íƒ€ì¼ (ê²Œì„ ë‚´ ì¸ë²¤í† ë¦¬ì™€ ë™ì¼)
    .panel-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 20px 24px;
      border-bottom: 2px solid var(--border-color);
      background: var(--bg-secondary);
      flex-shrink: 0;
      
      h3 {
        margin: 0;
        font-size: 22px;
        font-weight: 700;
        color: var(--text-primary);
        flex: 1;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      
      .close-btn {
        background: none;
        border: none;
        font-size: 32px;
        color: var(--text-secondary);
        cursor: pointer;
        width: 40px;
        height: 40px;
        min-width: 40px;
        min-height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        transition: all 0.2s ease;
        flex-shrink: 0;
        
        &:hover {
          background: rgba(255, 255, 255, 0.1);
          color: var(--text-primary);
        }
      }
    }
    
    // ìºë¦­í„° ì •ë³´ ë°”
    .character-info-bar {
      padding: 12px 24px;
      background: var(--bg-tertiary);
      border-bottom: 1px solid var(--border-color);
      
      .info-badges {
        display: flex;
        gap: 12px;
        
        .stat-badge {
          padding: 6px 14px;
          border-radius: 8px;
          font-size: 14px;
          font-weight: 600;
          display: inline-flex;
          align-items: center;
          gap: 4px;
          
          &.level {
            background: linear-gradient(135deg, #8b5cf6, #a78bfa);
            color: white;
            box-shadow: 0 2px 6px rgba(139, 92, 246, 0.3);
          }
          
          &.gold {
            background: linear-gradient(135deg, #fbbf24, #f59e0b);
            color: white;
            box-shadow: 0 2px 6px rgba(251, 191, 36, 0.3);
          }
        }
      }
    }
    
    // tab-content ìŠ¤íƒ€ì¼
    .tab-content {
      flex: 1;
      overflow-y: auto;
      padding: 24px;
      
      .inventory-sections {
        display: flex;
        flex-direction: column;
        gap: 32px;
        
        .equipment-section,
        .inventory-items-section {
          h3 {
            font-size: 22px;
            font-weight: 700;
            color: var(--text-primary);
            margin-bottom: 20px;
            padding-bottom: 12px;
            border-bottom: 2px solid var(--border-color);
          }
          
          .no-equipment,
          .no-items {
            text-align: center;
            padding: 40px;
            color: var(--text-secondary);
            font-size: 16px;
            background: var(--bg-secondary);
            border-radius: 12px;
          }
        }
      }
    }
    
    // í‘¸í„° ìŠ¤íƒ€ì¼
    .panel-footer {
      display: flex;
      justify-content: flex-end;
      padding: 20px 24px;
      border-top: 2px solid var(--border-color);
      background: var(--bg-secondary);
      flex-shrink: 0;
      
      .btn-secondary {
        padding: 10px 24px;
        background: var(--bg-tertiary);
        border: 1px solid var(--border-color);
        border-radius: 8px;
        color: var(--text-primary);
        font-size: 16px;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.2s ease;
        
        &:hover {
          background: var(--bg-primary);
          border-color: var(--primary-color);
          color: var(--primary-color);
        }
      }
    }
  }
}

